import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios'
Vue.use(Vuex);
const getters = {
    song: state => state.song,
    songList: state => state.songList,
    current: state => state.current,
    duration: state => state.duration,
    playing:state => state.playing,
};
const state = {
    index: 0,
    song: {
        id: 0,
        name: '',
        artists: '',
        picUrl:'',
        album: '',
        songUrl: '',
    },
    playing:true,
    current:0,
    duration:0,
    loop:false,
    songList: [],
};
const mutations = {
    setSong(state){
        state.song = state.songList[state.index];
    },
    setIndex(state, index){
        state.index = index
    },
    setLoop(state){
        state.loop = !state.loop
    },
    updateCurrent(state,time){
        state.current=time
    },
    updateDuration(state,time){
        state.duration=time
    },
    play(state){
        state.playing=true;
    },
    pause(state){
        state.playing=false;
    },
    playIndex (state, index) {
        state.index = index;
        state.song = state.songList[index]
    },
    playNext(state){
        if(state.index<state.songList.length-1){
            state.index++
        }else{
            state.index=0
        }
        state.song = state.songList[state.index];
    },
    playPrev(state){
        if(state.index>0){
            state.index--
        }else{
            state.index=state.songList.length-1
        }
        state.song = state.songList[state.index];
    },
    addSongList(state, item){
        let repeat = false;
        state.songList.forEach(function (element, index) {
            if (element.id == item.id) {
                state.index = index;
                repeat = true
            }
        });
        if (!repeat) {
            state.songList.push(item);
            state.index = state.songList.length-1;
        }
        if(state.songList.length===1){
            state.song=state.songList[state.index]
        }

    },
    remove(state, index){
        state.songList.splice(index,1)
    },
    removeAll(state){
        state.songList=[];
    }
};
const actions = {
    getSong({commit}, id){
        axios.get('http://119.29.196.41:5000/api/song', {params: {id: id}})
        .then(m => {
            if (m.data) {
                let res = m.data.data.songs[0];
                let song = {};
                song.id = res.id;
                song.name = res.name;
                song.artist = res.artists[0].name;
                song.picUrl= res.artists[0].picUrl;
                song.songUrl = res.mp3Url;
                commit('addSongList', song);
                commit('setSong');

            }
        });
    },
};

export default new Vuex.Store({
    getters,
    state,
    mutations,
    actions
})


