import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router);
export default new Router({
    mode: 'history',
    base: __dirname,
    routes: [
        {
            path: '/index',
            component: r => require.ensure([], () => r(require('../views/index.vue')), '/index'),
            children: [
                {
                    path: 'hotArtist',
                    name: 'hotArtist',
                    component: r => require.ensure([], () => r(require('../views/artist.vue')), '/index')
                },
                {
                    path: 'playlist',
                    name: 'playlist',
                    component: r => require.ensure([], () => r(require('../views/playList.vue')), '/index')
                },
                {
                    path: 'album',
                    name: 'album',
                    component: r => require.ensure([], () => r(require('../views/album.vue')), '/index')
                },
            ]
        },
        {
            path: '/playListPage/:id',
            name: 'playListPage',
            component: r => require.ensure([], () => r(require('../views/playListPage.vue')), '/')
        },
        {
            path: '/artistPage/:id',
            name: 'artistPage',
            component: r => require.ensure([], () => r(require('../views/artistPage.vue')), '/')
        },
        {
            path: '/albumPage/:id',
            name: 'albumPage',
            component: r => require.ensure([], () => r(require('../views/albumPage.vue')), '/')
        },
        {
            path: '*',
            name: '404',
            redirect: '/index/playlist'
        }
    ]
})



