<template>
    <div class="app" :class="{fixBottom:songList.length>0}">
        <keep-alive>
            <router-view class="view"></router-view>
        </keep-alive>
        <player v-if="songList.length>0"></player>
    </div>
</template>

<script>
    import {mapMutations, mapGetters} from 'vuex'
    import Player from './components/Player.vue'
    export default {
        data () {
            return {

            }
        },
        components:{Player},
        computed: {
            ...mapGetters([
                'songList',
            ])
        },
        methods: {
        }
    }
</script>

<style scoped>
    .app,.view{
        height:100%;
        overflow:hidden;
    }
    .fixBottom{
        padding-bottom:64px;
    }
</style>



