<template>
    <div>
        <mu-bottom-sheet :open="bottomSheet" @close="close" class="songList">
            <div class="songListTitle">
                播放列表（{{songList.length}}）
                <div class="right">
                   <span class="removeAll" @click="removeAll">
                        <mu-icon value="delete_sweep" :size="20"/>
                        清空
                    </span>
                </div>

            </div>
            <div class="songListBd">
                <mu-flexbox :gutter="0" v-for="(item,key) in songList" class="songListItem" :class="{active:song.id==item.id}">
                    <mu-flexbox-item basis="90%" class="textOverflow">
                        <div  @click="play(key)">{{item.name}}</div>
                    </mu-flexbox-item>
                    <mu-flexbox-item basis="14%">
                        <mu-icon value="clear" :size="24" @click="remove(key)"/>
                    </mu-flexbox-item>
                </mu-flexbox>
            </div>
        </mu-bottom-sheet>
    </div>
</template>
<script>
    import {mapMutations, mapGetters} from 'vuex'
    export default {
        data(){
            return {
                bottomSheet: false,
            }
        },
        computed: {
            ...mapGetters([
                'song',
                'songList',
            ])
        },
        methods: {
            close () {
                this.bottomSheet = false
            },
            open () {
                this.bottomSheet = true
            },
            play(index){
                this.$store.commit('playIndex', index);
            },
            remove(index){
                this.$store.commit('remove', index);
            },
            removeAll(){
                this.$store.commit('removeAll');
            }
        },
        watch:{
            song(){

            }
        }
    }
</script>
<style scoped>
    .songListTitle {
        font-size:20px;
        color:#979998;
        padding:10px;
        padding-left:16px;
        border-bottom:1px solid rgba(215, 219, 218, .8);
    }
    .songListTitle .right{
        float:right;
        font-size:16px;
    }
    .songListTitle .right .mu-icon{
        vertical-align:middle;
    }
    .songListBd {
        height:300px;
        overflow-x:hidden;
        overflow-y:auto;
        padding-left:16px;
    }

    .songListItem {
        font-size:18px;
        padding:10px 0;
        border-bottom:1px solid rgba(215, 219, 218, .8);
    }
    .songListItem.active{
        color:#CD3D3D
    }
    .songListItem:last-child {
        border:none;
    }
</style>


