<template>
    <div class="mplayer">
        <div class="progress">
            <mu-linear-progress :size="4" color="red" mode="determinate" :value="(current/duration)*100"/>
        </div>
        <mu-flexbox :gutter="0" class="flexbox">
            <mu-flexbox-item basis="20%" class="avatar">
                <mu-avatar :src="song.picUrl"/>
            </mu-flexbox-item>
            <mu-flexbox-item basis="50%">
                <audio id="audio" :src="song.songUrl" autoplay @ended="next" @timeupdate="timeUpdate" @error="error" :loop="loop"></audio>
                <!--<mu-slider class="slider" :value="(current/duration)*100" @change="change"/>-->
                <!--<p>{{song.artist}}-{{song.name}}<span>{{current}}/{{duration}}</span></p>-->
                <div class="name textOverflow">{{song.name}}</div>
                <div class="by textOverflow">{{song.artist}}</div>
            </mu-flexbox-item>
            <mu-flexbox-item basis="50%" class="avatar">
                <div class="btns">
                    <mu-icon value="playlist_play" :size="34" @click="showList"/>
                    <mu-icon :value="playing ? 'pause circle outline':'play_circle_outline'" :size="34" @click="toggle"/>
                    <mu-icon value="skip_next" :size="34" @click="next"/>
                </div>
            </mu-flexbox-item>
        </mu-flexbox>
        <list ref="SongList"></list>
    </div>
</template>
<script>
    import List from '../components/songList.vue'
    import {mapMutations, mapGetters} from 'vuex'
    export default {
        data(){
            return {
                loop: true,
                LoadError: false
            }
        },
        computed: {
            ...mapGetters([
                'song',
                'songList',
                'current',
                'duration',
                'playing'
            ])
        },
        components: {List},
        mounted(){

        },
        methods: {
            showList () {
                this.$refs.SongList.open()
            },
            next(){
                this.$store.commit('playNext');
            },
            timeUpdate(){
                let _this = this;
                let audio = document.getElementById('audio');
                if (this.songList.length > 0) {
                    this.$store.commit('updateCurrent', parseInt(audio.currentTime));
                    this.$store.commit('updateDuration', parseInt(audio.duration));
                }
            },
            toggle(){
                let audio = document.getElementById('audio');
                if (audio.paused) {
                    audio.play();
                    this.$store.commit('play');
                } else {
                    audio.pause();
                    this.$store.commit('pause');
                }
            },
            error(){
                window.alert('当前歌曲无版权！')
                //this.next();
            }
            //            change(value){
            //                let audio = document.getElementById('audio');
            //                let duration = parseInt(audio.duration);
            //                audio.currentTime = parseInt(duration * (value / 100));
            //                this.$store.commit('updateCurrent', parseInt(duration * (value / 100)));
            //            }
        },
        watch: {
            song(val){
                if (val) {
                    this.$store.commit('updateCurrent', 0);
                    this.$store.commit('play');
                    this.loop = this.songList == 1;
                }
            },
            songList(val){
                if (val) {


                }

            }
        }
    }
</script>
<style scoped>
    .mplayer {
        position:fixed;
        bottom:0;
        left:0;
        width:100%;
        /*height:50px;*/
        background:#fff;
    }

    .avatar {
        text-align:center;
    }

    .flexbox {
        padding:6px 0;
    }

    .name {
        font-size:18px;
        color:#303030;
    }

    .by { color:#979998 }
</style>


